body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background:-webkit-gradient(linear, left top, right top, from(rgb(60, 37, 218)), to(rgb(148, 227, 28)));
  background:-webkit-linear-gradient(left, rgb(60, 37, 218), rgb(148, 227, 28));
  background:linear-gradient(to right, rgb(60, 37, 218), rgb(148, 227, 28));
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@font-face {
    font-family: 'SEGA LOGO FONT';
    font-style: normal;
    font-weight: normal;
    src: local('SEGA LOGO FONT'), url(/robofriends/static/media/SEGA.12a3db8b.woff) format('woff');
    }
h1
{
font-family: 'SEGA LOGO FONT';
}
